.count-badge{
    width: $count-badge-width;
    height: $count-badge-height;
    background: $count-badge-bg;
    border-radius: $count-badge-border-radius;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $count-badge-color;
    font-size: $count-badge-font-size;
    line-height: 1;
}

@include media-breakpoint-up(xl){
    .header{
        &-section{
            .container{
                max-width: 1470px;
            }
        }
    }
}


@media (max-width: 1440px) {
    .header{
        &-top{
            &:after {
                width: 1220px;
            }
        }
        &-section{
            &:before{
                width: 220px;
                clip-path: polygon(0 0, 100% 0, 33% 100%, 0% 100%);
            }
        }
        &-logo{
            &.has-shape{
                &::before{
                    top: -100px;
                    right: 23px;
                    transform: rotate(48deg);
                }
                &::after {
                    width: 160px;
                    clip-path: polygon(0 0, 101% 0, 53% 100%, 0% 100%);
                }
            }
        }
    }
}


